"use client";

import { useState } from "react";
import { Edit, Trash2, Building2 } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";
import { useParkingStore } from "@/hooks/use-parking-store";
import { toast } from "sonner";
import { EditFacilityForm } from "./editFacilityForm";
import { Facility } from "@/lib/schemas";

export function FacilityManagement() {
  const [selectedFacility, setSelectedFacility] = useState<Facility | null>(
    null
  );
  const [isEditDialogOpen, setIsEditDialogOpen] = useState(false);
  const facilities = useParkingStore((state) => state.facilities);
  const getFloorsByFacility = useParkingStore(
    (state) => state.getFloorsByFacility
  );
  const deleteFacility = useParkingStore((state) => state.deleteFacility);

  const handleDeleteFacility = (facilityId: string, facilityName: string) => {
    if (
      window.confirm(
        `Are you sure you want to delete "${facilityName}"? This will also delete all floors and bookings.`
      )
    ) {
      deleteFacility(facilityId);
      toast.success("Facility deleted successfully");
    }
  };

  const handleEditSuccess = () => {
    setIsEditDialogOpen(false);
    setSelectedFacility(null);
    toast.success("Facility updated successfully");
  };

  if (facilities.length === 0) {
    return (
      <Card>
        <CardContent className="flex flex-col items-center justify-center py-12">
          <Building2 className="h-16 w-16 text-muted-foreground mb-4" />
          <h3 className="text-lg font-semibold mb-2">No facilities found</h3>
          <p className="text-muted-foreground text-center mb-4">
            Create your first facility to start managing parking.
          </p>
        </CardContent>
      </Card>
    );
  }

  return (
    <div className="space-y-4">
      <div className="flex items-center justify-between">
        <h2 className="text-2xl font-bold">Facility Management</h2>
        <Badge variant="secondary">{facilities.length} facilities</Badge>
      </div>

      <div className="grid gap-4">
        {facilities.map((facility) => {
          const floors = getFloorsByFacility(facility.id);
          const totalSlots = floors.reduce(
            (sum, floor) => sum + floor.rows * floor.columns,
            0
          );

          return (
            <Card
              key={facility.id}
              className="hover:shadow-md transition-shadow"
            >
              <CardHeader className="pb-3">
                <div className="flex items-start justify-between">
                  <div>
                    <CardTitle className="text-lg">
                      {facility.name || "Unnamed Facility"}
                    </CardTitle>
                    <p className="text-sm text-muted-foreground">
                      {facility.address || "No address provided"}
                    </p>
                  </div>
                  <Badge variant="outline" className="capitalize">
                    {facility.facilityType || "unknown"}
                  </Badge>
                </div>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-sm">
                  <div>
                    <p className="font-medium">Floors</p>
                    <p className="text-muted-foreground">
                      {floors.length}/{facility.totalFloors || 0}
                    </p>
                  </div>
                  <div>
                    <p className="font-medium">Total Slots</p>
                    <p className="text-muted-foreground">{totalSlots}</p>
                  </div>
                  <div>
                    <p className="font-medium">Price/Hour</p>
                    <p className="text-green-600 font-semibold">
                      $
                      {facility.pricePerHour
                        ? facility.pricePerHour.toFixed(2)
                        : "0.00"}
                    </p>
                  </div>
                  <div>
                    <p className="font-medium">Hours</p>
                    <p className="text-muted-foreground">
                      {facility.openingTime || "N/A"} -{" "}
                      {facility.closingTime || "N/A"}
                    </p>
                  </div>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-4 text-sm">
                  <div>
                    <p className="font-medium">Contact</p>
                    <p className="text-muted-foreground">
                      {facility.contactPhone || "No phone provided"}
                    </p>
                    <p className="text-muted-foreground">
                      {facility.contactEmail || "No email provided"}
                    </p>
                  </div>
                  <div>
                    <p className="font-medium">Location</p>
                    <p className="text-muted-foreground">
                      {facility.latitude && facility.longitude
                        ? `${facility.latitude.toFixed(
                            4
                          )}, ${facility.longitude.toFixed(4)}`
                        : "No coordinates provided"}
                    </p>
                  </div>
                </div>

                {facility.amenities && facility.amenities.length > 0 && (
                  <div>
                    <p className="font-medium text-sm mb-2">Amenities</p>
                    <div className="flex flex-wrap gap-1">
                      {facility.amenities.map((amenity) => (
                        <Badge
                          key={amenity}
                          variant="secondary"
                          className="text-xs"
                        >
                          {amenity}
                        </Badge>
                      ))}
                    </div>
                  </div>
                )}

                <div className="flex gap-2 pt-2">
                  <Dialog
                    open={isEditDialogOpen}
                    onOpenChange={setIsEditDialogOpen}
                  >
                    <DialogTrigger asChild>
                      <Button
                        variant="outline"
                        size="sm"
                        onClick={() => setSelectedFacility(facility)}
                      >
                        <Edit className="h-4 w-4 mr-1" />
                        Edit
                      </Button>
                    </DialogTrigger>
                    <DialogContent className="max-w-2xl max-h-[90vh] overflow-y-auto">
                      <DialogHeader>
                        <DialogTitle>Edit Facility</DialogTitle>
                      </DialogHeader>
                      {selectedFacility && (
                        <EditFacilityForm
                          facility={selectedFacility}
                          onSuccess={handleEditSuccess}
                          onCancel={() => setIsEditDialogOpen(false)}
                        />
                      )}
                    </DialogContent>
                  </Dialog>

                  <Button
                    variant="destructive"
                    size="sm"
                    onClick={() =>
                      handleDeleteFacility(
                        facility.id,
                        facility.name || "Unnamed Facility"
                      )
                    }
                  >
                    <Trash2 className="h-4 w-4 mr-1" />
                    Delete
                  </Button>
                </div>
              </CardContent>
            </Card>
          );
        })}
      </div>
    </div>
  );
}
