"use client";

import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { Clock, Phone, Mail } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Checkbox } from "@/components/ui/checkbox";
import {
  createFacilitySchema,
  type CreateFacilityInput,
  type Facility,
} from "@/lib/schemas";
import { useParkingStore } from "@/hooks/use-parking-store";

interface EditFacilityFormProps {
  facility: Facility;
  onSuccess?: () => void;
  onCancel?: () => void;
}

const facilityTypes = [
  { value: "mall", label: "Shopping Mall" },
  { value: "office", label: "Office Building" },
  { value: "hospital", label: "Hospital" },
  { value: "airport", label: "Airport" },
  { value: "residential", label: "Residential" },
  { value: "commercial", label: "Commercial" },
];

const availableAmenities = [
  "24/7 Security",
  "CCTV Surveillance",
  "Electric Vehicle Charging",
  "Covered Parking",
  "Valet Service",
  "Car Wash",
  "Restrooms",
  "Elevator Access",
  "Disabled Access",
  "Online Booking",
];

export function EditFacilityForm({
  facility,
  onSuccess,
  onCancel,
}: EditFacilityFormProps) {
  const updateFacility = useParkingStore((state) => state.updateFacility);

  const {
    register,
    handleSubmit,
    setValue,
    watch,
    formState: { errors, isSubmitting },
  } = useForm<CreateFacilityInput>({
    resolver: zodResolver(createFacilitySchema),
    defaultValues: {
      name: facility.name,
      address: facility.address,
      facilityType: facility.facilityType,
      amenities: facility.amenities || [],
      openingTime: facility.openingTime,
      closingTime: facility.closingTime,
      contactPhone: facility.contactPhone,
      contactEmail: facility.contactEmail,
      latitude: facility.latitude,
      longitude: facility.longitude,
      totalFloors: facility.totalFloors,
      pricePerHour: facility.pricePerHour,
    },
  });

  const selectedAmenities = watch("amenities");

  const onSubmit = async (data: CreateFacilityInput) => {
    try {
      updateFacility(facility.id, data);
      onSuccess?.();
    } catch (error) {
      console.error("Failed to update facility:", error);
    }
  };

  const handleAmenityChange = (amenity: string, checked: boolean) => {
    const current = selectedAmenities || [];
    if (checked) {
      setValue("amenities", [...current, amenity]);
    } else {
      setValue(
        "amenities",
        current.filter((a) => a !== amenity)
      );
    }
  };

  return (
    <form onSubmit={handleSubmit(onSubmit)} className="space-y-4">
      <div className="grid grid-cols-2 gap-4">
        <div className="space-y-2">
          <Label htmlFor="name">Facility Name</Label>
          <Input id="name" {...register("name")} />
          {errors.name && (
            <p className="text-sm text-red-500">{errors.name.message}</p>
          )}
        </div>

        <div className="space-y-2">
          <Label htmlFor="facilityType">Facility Type</Label>
          <Select
            value={watch("facilityType")}
            onValueChange={(value: any) => setValue("facilityType", value)}
          >
            <SelectTrigger>
              <SelectValue />
            </SelectTrigger>
            <SelectContent>
              {facilityTypes.map((type) => (
                <SelectItem key={type.value} value={type.value}>
                  {type.label}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>
      </div>

      <div className="space-y-2">
        <Label htmlFor="address">Address</Label>
        <Input id="address" {...register("address")} />
        {errors.address && (
          <p className="text-sm text-red-500">{errors.address.message}</p>
        )}
      </div>

      <div className="grid grid-cols-2 gap-4">
        <div className="space-y-2">
          <Label htmlFor="latitude">Latitude</Label>
          <Input
            id="latitude"
            type="number"
            step="any"
            {...register("latitude", { valueAsNumber: true })}
          />
          {errors.latitude && (
            <p className="text-sm text-red-500">{errors.latitude.message}</p>
          )}
        </div>

        <div className="space-y-2">
          <Label htmlFor="longitude">Longitude</Label>
          <Input
            id="longitude"
            type="number"
            step="any"
            {...register("longitude", { valueAsNumber: true })}
          />
          {errors.longitude && (
            <p className="text-sm text-red-500">{errors.longitude.message}</p>
          )}
        </div>
      </div>

      <div className="grid grid-cols-2 gap-4">
        <div className="space-y-2">
          <Label htmlFor="contactPhone">Contact Phone</Label>
          <div className="relative">
            <Phone className="absolute left-3 top-3 h-4 w-4 text-muted-foreground" />
            <Input
              id="contactPhone"
              {...register("contactPhone")}
              className="pl-10"
            />
          </div>
          {errors.contactPhone && (
            <p className="text-sm text-red-500">
              {errors.contactPhone.message}
            </p>
          )}
        </div>

        <div className="space-y-2">
          <Label htmlFor="contactEmail">Contact Email</Label>
          <div className="relative">
            <Mail className="absolute left-3 top-3 h-4 w-4 text-muted-foreground" />
            <Input
              id="contactEmail"
              type="email"
              {...register("contactEmail")}
              className="pl-10"
            />
          </div>
          {errors.contactEmail && (
            <p className="text-sm text-red-500">
              {errors.contactEmail.message}
            </p>
          )}
        </div>
      </div>

      <div className="grid grid-cols-3 gap-4">
        <div className="space-y-2">
          <Label htmlFor="openingTime">Opening Time</Label>
          <div className="relative">
            <Clock className="absolute left-3 top-3 h-4 w-4 text-muted-foreground" />
            <Input
              id="openingTime"
              type="time"
              {...register("openingTime")}
              className="pl-10"
            />
          </div>
          {errors.openingTime && (
            <p className="text-sm text-red-500">{errors.openingTime.message}</p>
          )}
        </div>

        <div className="space-y-2">
          <Label htmlFor="closingTime">Closing Time</Label>
          <div className="relative">
            <Clock className="absolute left-3 top-3 h-4 w-4 text-muted-foreground" />
            <Input
              id="closingTime"
              type="time"
              {...register("closingTime")}
              className="pl-10"
            />
          </div>
          {errors.closingTime && (
            <p className="text-sm text-red-500">{errors.closingTime.message}</p>
          )}
        </div>

        <div className="space-y-2">
          <Label htmlFor="pricePerHour">Price per Hour ($)</Label>
          <Input
            id="pricePerHour"
            type="number"
            min="0"
            step="0.01"
            {...register("pricePerHour", { valueAsNumber: true })}
          />
          {errors.pricePerHour && (
            <p className="text-sm text-red-500">
              {errors.pricePerHour.message}
            </p>
          )}
        </div>
      </div>

      <div className="space-y-2">
        <Label htmlFor="totalFloors">Total Floors</Label>
        <Input
          id="totalFloors"
          type="number"
          min="4"
          {...register("totalFloors", { valueAsNumber: true })}
        />
        {errors.totalFloors && (
          <p className="text-sm text-red-500">{errors.totalFloors.message}</p>
        )}
      </div>

      <div className="space-y-2">
        <Label>Amenities</Label>
        <div className="grid grid-cols-2 gap-2 max-h-32 overflow-y-auto">
          {availableAmenities.map((amenity) => (
            <div key={amenity} className="flex items-center space-x-2">
              <Checkbox
                id={amenity}
                checked={selectedAmenities?.includes(amenity) || false}
                onCheckedChange={(checked) =>
                  handleAmenityChange(amenity, checked as boolean)
                }
              />
              <Label htmlFor={amenity} className="text-sm">
                {amenity}
              </Label>
            </div>
          ))}
        </div>
      </div>

      <div className="flex gap-2 pt-4">
        <Button
          type="button"
          variant="outline"
          onClick={onCancel}
          className="flex-1 bg-transparent"
        >
          Cancel
        </Button>
        <Button type="submit" disabled={isSubmitting} className="flex-1">
          {isSubmitting ? "Updating..." : "Update Facility"}
        </Button>
      </div>
    </form>
  );
}
