import { z } from "zod";

// Base schemas
export const facilitySchema = z.object({
  id: z.string(),
  name: z.string().min(1, "Facility name is required"),
  address: z.string().min(1, "Address is required"),
  facilityType: z.enum([
    "mall",
    "office",
    "hospital",
    "airport",
    "residential",
    "commercial",
  ]),
  amenities: z.array(z.string()).default([]),
  openingTime: z
    .string()
    .regex(/^([0-1]?[0-9]|2[0-3]):[0-5][0-9]$/, "Invalid time format"),
  closingTime: z
    .string()
    .regex(/^([0-1]?[0-9]|2[0-3]):[0-5][0-9]$/, "Invalid time format"),
  contactPhone: z.string().min(10, "Valid phone number is required"),
  contactEmail: z.string().email("Valid email is required"),
  latitude: z.number().min(-90).max(90),
  longitude: z.number().min(-180).max(180),
  totalFloors: z.number().min(4, "Minimum 4 floors required"),
  pricePerHour: z.number().min(0, "Price must be positive"),
  createdAt: z.date().default(() => new Date()),
});

export const floorSchema = z.object({
  id: z.string(),
  facilityId: z.string(),
  floorNumber: z.number().min(1),
  name: z.string().min(1, "Floor name is required"),
  rows: z.number().min(1, "At least 1 row required"),
  columns: z.number().min(1, "At least 1 column required"),
  createdAt: z.date().default(() => new Date()),
});

export const slotSchema = z.object({
  id: z.string(),
  facilityId: z.string(),
  floorId: z.string(),
  slotNumber: z.string(),
  row: z.number(),
  column: z.number(),
  isAvailable: z.boolean().default(true),
  isReserved: z.boolean().default(false),
  vehicleType: z.enum(["car", "motorcycle", "truck"]).default("car"),
});

export const bookingSchema = z.object({
  id: z.string(),
  facilityId: z.string(),
  floorId: z.string(),
  slotId: z.string(),
  customerName: z.string().min(1, "Customer name is required"),
  customerEmail: z.string().email("Valid email is required"),
  customerPhone: z.string().min(10, "Valid phone number is required"),
  vehicleNumber: z.string().min(1, "Vehicle number is required"),
  vehicleType: z.enum(["car", "motorcycle", "truck"]),
  startTime: z.date(),
  endTime: z.date(),
  status: z.enum(["active", "completed", "cancelled"]).default("active"),
  createdAt: z.date().default(() => new Date()),
});

export const paymentSchema = z.object({
  id: z.string(),
  bookingId: z.string(),
  amount: z.number().min(0),
  currency: z.string().default("USD"),
  paymentMethod: z.enum(["card", "paypal", "apple_pay", "google_pay"]),
  cardNumber: z.string().optional(),
  expiryDate: z.string().optional(),
  cvv: z.string().optional(),
  cardholderName: z.string().optional(),
  status: z
    .enum(["pending", "completed", "failed", "refunded"])
    .default("pending"),
  transactionId: z.string().optional(),
  createdAt: z.date().default(() => new Date()),
});

export const locationSearchSchema = z.object({
  query: z.string().min(1, "Location is required"),
  radius: z
    .preprocess((val) => {
      if (typeof val === "string" || typeof val === "number") {
        return Number(val);
      }
      return 10;
    }, z.number().min(1).max(50)),
  latitude: z.number().optional(),
  longitude: z.number().optional(),
});



// Form schemas
export const createFacilitySchema = facilitySchema.omit({
  id: true,
  createdAt: true,
});
export const createFloorSchema = floorSchema.omit({
  id: true,
  facilityId: true,
  createdAt: true,
});
export const createBookingSchema = bookingSchema.omit({
  id: true,
  createdAt: true,
  status: true,
});

export const createFacilityWithFloorsSchema = z.object({
  facility: facilitySchema.omit({ id: true, createdAt: true }),
  floors: z
    .array(
      floorSchema.omit({ id: true, facilityId: true, createdAt: true }).extend({
        floorNumber: z.number().optional(),
      })
    )
    .min(4, "Minimum 4 floors required"),
});

export const createPaymentSchema = paymentSchema.omit({
  id: true,
  createdAt: true,
  status: true,
  transactionId: true,
});

// Inferred types
export type Facility = z.infer<typeof facilitySchema>;
export type Floor = z.infer<typeof floorSchema>;
export type Slot = z.infer<typeof slotSchema>;
export type Booking = z.infer<typeof bookingSchema>;
export type Payment = z.infer<typeof paymentSchema>;
export type LocationSearch = z.infer<typeof locationSearchSchema>;
export type CreateFacilityInput = z.infer<typeof createFacilitySchema>;
export type CreateFloorInput = z.infer<typeof createFloorSchema>;
export type CreateBookingInput = z.infer<typeof createBookingSchema>;
export type CreateFacilityWithFloorsInput = z.infer<
  typeof createFacilityWithFloorsSchema
>;
export type CreatePaymentInput = z.infer<typeof createPaymentSchema>;
