import { create } from "zustand"
import { persist } from "zustand/middleware"
import type { Facility, Floor, Slot, Booking, Payment } from "@/lib/schemas"
import { generateSlotId, calculateDistance, generateSlotNumber } from "@/lib/settings"

interface ParkingStore {
  facilities: Facility[]
  floors: Floor[]
  slots: Slot[]
  bookings: Booking[]
  payments: Payment[]

  // Facility actions
  addFacility: (facility: Omit<Facility, "id" | "createdAt">) => void
  updateFacility: (id: string, facility: Partial<Facility>) => void
  deleteFacility: (id: string) => void
  addFacilityWithFloors: (data: CreateFacilityWithFloorsInput) => void
  searchFacilitiesByLocation: (lat: number, lng: number, radius: number) => Facility[]

  // Floor actions
  addFloor: (floor: Omit<Floor, "id" | "createdAt">) => void
  updateFloor: (id: string, floor: Partial<Floor>) => void
  deleteFloor: (id: string) => void

  // Slot actions
  generateSlotsForFloor: (floorId: string, facilityId: string, rows: number, columns: number) => void
  updateSlot: (id: string, slot: Partial<Slot>) => void

  // Booking actions
  addBooking: (booking: Omit<Booking, "id" | "createdAt">) => void
  updateBooking: (id: string, booking: Partial<Booking>) => void
  cancelBooking: (id: string) => void

  // Payment actions
  addPayment: (payment: Omit<Payment, "id" | "createdAt">) => void
  updatePayment: (id: string, payment: Partial<Payment>) => void

  // Getters
  getFacilityById: (id: string) => Facility | undefined
  getFloorsByFacility: (facilityId: string) => Floor[]
  getSlotsByFloor: (floorId: string) => Slot[]
  getBookingsBySlot: (slotId: string) => Booking[]
  getAvailableSlots: (floorId: string) => Slot[]
}

interface CreateFacilityWithFloorsInput {
  facility: Omit<Facility, "id" | "createdAt">
  floors: Array<Omit<Floor, "id" | "createdAt" | "facilityId"> & { floorNumber?: number }>
}

export const useParkingStore = create<ParkingStore>()(
  persist(
    (set, get) => ({
      facilities: [],
      floors: [],
      slots: [],
      bookings: [],
      payments: [],

      addFacility: (facility) => {
        const newFacility: Facility = {
          ...facility,
          id: generateSlotId(),
          createdAt: new Date(),
        }
        set((state) => ({
          facilities: [...state.facilities, newFacility],
        }))
      },

      updateFacility: (id, facility) => {
        set((state) => ({
          facilities: state.facilities.map((f) => (f.id === id ? { ...f, ...facility } : f)),
        }))
      },

      deleteFacility: (id) => {
        set((state) => ({
          facilities: state.facilities.filter((f) => f.id !== id),
          floors: state.floors.filter((f) => f.facilityId !== id),
          slots: state.slots.filter((s) => s.facilityId !== id),
          bookings: state.bookings.filter((b) => b.facilityId !== id),
        }))
      },

      addFacilityWithFloors: (data) => {
        const facilityId = generateSlotId()
        const newFacility: Facility = {
          ...data.facility,
          id: facilityId,
          createdAt: new Date(),
        }

        const newFloors: Floor[] = data.floors.map((floor, index) => ({
          ...floor,
          id: generateSlotId(),
          facilityId,
          floorNumber: floor.floorNumber || index + 1,
          createdAt: new Date(),
        }))

        set((state) => ({
          facilities: [...state.facilities, newFacility],
          floors: [...state.floors, ...newFloors],
        }))

        // Generate slots for all floors
        newFloors.forEach((floor) => {
          get().generateSlotsForFloor(floor.id, facilityId, floor.rows, floor.columns)
        })
      },

      searchFacilitiesByLocation: (lat, lng, radius) => {
        const facilities = get().facilities
        return facilities.filter((facility) => {
          if (!facility.latitude || !facility.longitude) return false
          const distance = calculateDistance(lat, lng, facility.latitude, facility.longitude)
          return distance <= radius
        })
      },

      addFloor: (floor) => {
        const newFloor: Floor = {
          ...floor,
          id: generateSlotId(),
          createdAt: new Date(),
        }
        set((state) => ({
          floors: [...state.floors, newFloor],
        }))

        // Generate slots for the new floor
        get().generateSlotsForFloor(newFloor.id, floor.facilityId, floor.rows, floor.columns)
      },

      updateFloor: (id, floor) => {
        set((state) => ({
          floors: state.floors.map((f) => (f.id === id ? { ...f, ...floor } : f)),
        }))
      },

      deleteFloor: (id) => {
        set((state) => ({
          floors: state.floors.filter((f) => f.id !== id),
          slots: state.slots.filter((s) => s.floorId !== id),
          bookings: state.bookings.filter((b) => b.floorId !== id),
        }))
      },

      generateSlotsForFloor: (floorId, facilityId, rows, columns) => {
        const newSlots: Slot[] = []

        for (let row = 1; row <= rows; row++) {
          for (let column = 1; column <= columns; column++) {
            newSlots.push({
              id: generateSlotId(),
              facilityId,
              floorId,
              slotNumber: generateSlotNumber(row, column),
              row,
              column,
              isAvailable: true,
              isReserved: false,
              vehicleType: "car",
            })
          }
        }

        set((state) => ({
          slots: [...state.slots, ...newSlots],
        }))
      },

      updateSlot: (id, slot) => {
        set((state) => ({
          slots: state.slots.map((s) => (s.id === id ? { ...s, ...slot } : s)),
        }))
      },

      addBooking: (booking) => {
        const newBooking: Booking = {
          ...booking,
          id: generateSlotId(),
          createdAt: new Date(),
          status: "active",
        }

        set((state) => ({
          bookings: [...state.bookings, newBooking],
          slots: state.slots.map((s) => (s.id === booking.slotId ? { ...s, isAvailable: false, isReserved: true } : s)),
        }))
      },

      updateBooking: (id, booking) => {
        set((state) => ({
          bookings: state.bookings.map((b) => (b.id === id ? { ...b, ...booking } : b)),
        }))
      },

      cancelBooking: (id) => {
        const booking = get().bookings.find((b) => b.id === id)
        if (booking) {
          set((state) => ({
            bookings: state.bookings.map((b) => (b.id === id ? { ...b, status: "cancelled" as const } : b)),
            slots: state.slots.map((s) =>
              s.id === booking.slotId ? { ...s, isAvailable: true, isReserved: false } : s,
            ),
          }))
        }
      },

      addPayment: (payment) => {
        const newPayment: Payment = {
          ...payment,
          id: generateSlotId(),
          createdAt: new Date(),
          status: "pending",
        }
        set((state) => ({
          payments: [...state.payments, newPayment],
        }))
      },

      updatePayment: (id, payment) => {
        set((state) => ({
          payments: state.payments.map((p) => (p.id === id ? { ...p, ...payment } : p)),
        }))
      },

      getFacilityById: (id) => {
        return get().facilities.find((f) => f.id === id)
      },

      getFloorsByFacility: (facilityId) => {
        return get().floors.filter((f) => f.facilityId === facilityId)
      },

      getSlotsByFloor: (floorId) => {
        return get().slots.filter((s) => s.floorId === floorId)
      },

      getBookingsBySlot: (slotId) => {
        return get().bookings.filter((b) => b.slotId === slotId && b.status === "active")
      },

      getAvailableSlots: (floorId) => {
        return get().slots.filter((s) => s.floorId === floorId && s.isAvailable)
      },
    }),
    {
      name: "parking-store",
    },
  ),
)

