/* eslint-disable @typescript-eslint/no-explicit-any */
"use client";

import { useState } from "react";
import {
  Calendar,
  Car,
  Clock,
  MapPin,
  CreditCard,
  X,
  Grid3X3,
  List,
  MoreVertical,
} from "lucide-react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { useParkingStore } from "@/hooks/use-parking-store";
import { toast } from "sonner";
import { format } from "date-fns";

type ViewMode = "grid" | "list";

export function MyBookings() {
  const [ setSelectedBooking] = useState<any>(null);
  const [viewMode, setViewMode] = useState<ViewMode>("grid");
  const bookings = useParkingStore((state) => state.bookings);
  const facilities = useParkingStore((state) => state.facilities);
  const floors = useParkingStore((state) => state.floors);
  const slots = useParkingStore((state) => state.slots);
  const payments = useParkingStore((state) => state.payments);
  const cancelBooking = useParkingStore((state) => state.cancelBooking);

  const getBookingDetails = (booking: any) => {
    const facility = facilities.find((f) => f.id === booking.facilityId);
    const floor = floors.find((f) => f.id === booking.floorId);
    const slot = slots.find((s) => s.id === booking.slotId);
    const payment = payments.find((p) => p.bookingId === booking.id);

    return { facility, floor, slot, payment };
  };

  const handleCancelBooking = (bookingId: string) => {
    cancelBooking(bookingId);
    toast.success("Booking cancelled successfully");
    setSelectedBooking(null);
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case "active":
        return "bg-green-100 text-green-800";
      case "completed":
        return "bg-blue-100 text-blue-800";
      case "cancelled":
        return "bg-red-100 text-red-800";
      default:
        return "bg-gray-100 text-gray-800";
    }
  };

  const calculateDuration = (startTime: Date, endTime: Date) => {
    const duration = Math.ceil(
      (endTime.getTime() - startTime.getTime()) / (1000 * 60 * 60)
    );
    return duration;
  };

  const calculateAmount = (booking: any, facility: any) => {
    const duration = calculateDuration(
      new Date(booking.startTime),
      new Date(booking.endTime)
    );
    return duration * (facility?.pricePerHour || 0);
  };

  const formatDateShort = (date: Date) => {
    return new Intl.DateTimeFormat("en-US", {
      month: "short",
      day: "numeric",
      hour: "2-digit",
      minute: "2-digit",
    }).format(date);
  };

  if (bookings.length === 0) {
    return (
      <Card>
        <CardContent className="flex flex-col items-center justify-center py-12">
          <Car className="h-16 w-16 text-muted-foreground mb-4" />
          <h3 className="text-lg font-semibold mb-2">No bookings found</h3>
          <p className="text-muted-foreground text-center mb-4">
            You haven&#39;t made any parking reservations yet.
          </p>
          <Button onClick={() => window.location.reload()}>
            Book Parking Now
          </Button>
        </CardContent>
      </Card>
    );
  }

  const BookingDetailsModal = ({ booking }: { booking: any }) => {
    const { facility, floor, slot, payment } = getBookingDetails(booking);

    return (
      <DialogContent className="max-w-md">
        <DialogHeader>
          <DialogTitle>Booking Details</DialogTitle>
        </DialogHeader>
        <div className="space-y-4">
          <div>
            <h4 className="font-semibold mb-2">Location</h4>
            <p className="text-sm">{facility?.name}</p>
            <p className="text-sm text-muted-foreground">{facility?.address}</p>
            <p className="text-sm text-muted-foreground">
              {floor?.name}  Slot {slot?.slotNumber}
            </p>
          </div>

          <div>
            <h4 className="font-semibold mb-2">Customer Information</h4>
            <p className="text-sm">{booking.customerName}</p>
            <p className="text-sm text-muted-foreground">
              {booking.customerEmail}
            </p>
            <p className="text-sm text-muted-foreground">
              {booking.customerPhone}
            </p>
          </div>

          <div>
            <h4 className="font-semibold mb-2">Vehicle Information</h4>
            <p className="text-sm">{booking.vehicleNumber}</p>
            <p className="text-sm text-muted-foreground capitalize">
              {booking.vehicleType}
            </p>
          </div>

          <div>
            <h4 className="font-semibold mb-2">Timing</h4>
            <p className="text-sm">
              <p>Start: {format(new Date(booking.startTime), "PPPp")}</p>
            </p>
            <p className="text-sm">
              End: {format(new Date(booking.endTime), "PPPp")}
            </p>
            <p className="text-sm text-muted-foreground">
              Duration:{" "}
              {calculateDuration(
                new Date(booking.startTime),
                new Date(booking.endTime)
              )}{" "}
              hours
            </p>
          </div>

          <div>
            <h4 className="font-semibold mb-2">Payment</h4>
            <p className="text-sm font-semibold text-green-600">
              ${calculateAmount(booking, facility).toFixed(2)}
            </p>
            <p className="text-sm text-muted-foreground">
              Status: {payment?.status || "Pending"}
            </p>
          </div>

          {booking.status === "active" && (
            <Button
              variant="destructive"
              size="sm"
              onClick={() => handleCancelBooking(booking.id)}
              className="w-full"
            >
              Cancel Booking
            </Button>
          )}
        </div>
      </DialogContent>
    );
  };

  const GridView = () => (
    <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
      {bookings.map((booking) => {
        const { facility, floor, slot } = getBookingDetails(booking);

        return (
          <Card key={booking.id} className="hover:shadow-md transition-shadow">
            <CardHeader className="pb-3">
              <div className="flex items-start justify-between">
                <div className="min-w-0 flex-1">
                  <CardTitle className="text-lg truncate">
                    {facility?.name || "Unknown Facility"}
                  </CardTitle>
                  <p className="text-sm text-muted-foreground truncate">
                    {floor?.name}  Slot {slot?.slotNumber}
                  </p>
                </div>
                <Badge className={getStatusColor(booking.status)}>
                  {booking.status}
                </Badge>
              </div>
            </CardHeader>
            <CardContent className="space-y-3">
              <div className="grid grid-cols-2 gap-4">
                <div className="flex items-center gap-2 text-sm">
                  <Calendar className="h-4 w-4 text-muted-foreground flex-shrink-0" />
                  <div className="min-w-0">
                    <p className="font-medium">Start</p>
                    <p className="text-muted-foreground truncate">
                      {formatDateShort(new Date(booking.startTime))}
                    </p>
                  </div>
                </div>
                <div className="flex items-center gap-2 text-sm">
                  <Clock className="h-4 w-4 text-muted-foreground flex-shrink-0" />
                  <div className="min-w-0">
                    <p className="font-medium">End</p>
                    <p className="text-muted-foreground truncate">
                      {formatDateShort(new Date(booking.endTime))}
                    </p>
                  </div>
                </div>
              </div>

              <div className="flex items-center gap-2 text-sm">
                <MapPin className="h-4 w-4 text-muted-foreground flex-shrink-0" />
                <span className="text-muted-foreground truncate">
                  {facility?.address}
                </span>
              </div>

              <div className="flex items-center gap-2 text-sm">
                <Car className="h-4 w-4 text-muted-foreground flex-shrink-0" />
                <span className="text-muted-foreground truncate">
                  {booking.vehicleNumber}  {booking.vehicleType}
                </span>
              </div>

              <div className="flex items-center gap-2 text-sm">
                <CreditCard className="h-4 w-4 text-muted-foreground flex-shrink-0" />
                <span className="font-semibold text-green-600">
                  ${calculateAmount(booking, facility).toFixed(2)}
                </span>
                <span className="text-muted-foreground">
                  (
                  {calculateDuration(
                    new Date(booking.startTime),
                    new Date(booking.endTime)
                  )}{" "}
                  hours)
                </span>
              </div>

              <div className="flex gap-2 pt-2">
                <Dialog>
                  <DialogTrigger asChild>
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => setSelectedBooking(booking)}
                      className="flex-1"
                    >
                      View Details
                    </Button>
                  </DialogTrigger>
                  <BookingDetailsModal booking={booking} />
                </Dialog>

                {booking.status === "active" && (
                  <Button
                    variant="destructive"
                    size="sm"
                    onClick={() => handleCancelBooking(booking.id)}
                  >
                    <X className="h-4 w-4" />
                  </Button>
                )}
              </div>
            </CardContent>
          </Card>
        );
      })}
    </div>
  );

  const ListView = () => (
    <div className="space-y-2">
      {bookings.map((booking) => {
        const { facility, floor, slot} = getBookingDetails(booking);

        return (
          <Card key={booking.id} className="hover:shadow-sm transition-shadow">
            <CardContent className="p-4">
              <div className="flex items-center justify-between gap-4">
                {/* Left section - Facility info */}
                <div className="flex items-center gap-3 min-w-0 flex-1">
                  <div className="flex-shrink-0">
                    <div className="w-10 h-10 bg-primary/10 rounded-lg flex items-center justify-center">
                      <Car className="h-5 w-5 text-primary" />
                    </div>
                  </div>
                  <div className="min-w-0 flex-1">
                    <div className="flex items-center gap-2 mb-1">
                      <h3 className="font-semibold truncate">
                        {facility?.name || "Unknown Facility"}
                      </h3>
                      <Badge
                        className={`${getStatusColor(booking.status)} text-xs`}
                      >
                        {booking.status}
                      </Badge>
                    </div>
                    <div className="flex items-center gap-4 text-sm text-muted-foreground">
                      <span className="truncate">
                        {floor?.name}  Slot {slot?.slotNumber}
                      </span>
                      <span className="truncate">{booking.vehicleNumber}</span>
                    </div>
                  </div>
                </div>

                {/* Middle section - Time info */}
                <div className="hidden md:flex items-center gap-6 text-sm">
                  <div className="text-center">
                    <p className="font-medium">Start</p>
                    <p className="text-muted-foreground">
                      {formatDateShort(new Date(booking.startTime))}
                    </p>
                  </div>
                  <div className="text-center">
                    <p className="font-medium">End</p>
                    <p className="text-muted-foreground">
                      {formatDateShort(new Date(booking.endTime))}
                    </p>
                  </div>
                  <div className="text-center">
                    <p className="font-medium">Duration</p>
                    <p className="text-muted-foreground">
                      {calculateDuration(
                        new Date(booking.startTime),
                        new Date(booking.endTime)
                      )}
                      h
                    </p>
                  </div>
                </div>

                {/* Right section - Price and actions */}
                <div className="flex items-center gap-3">
                  <div className="text-right hidden sm:block">
                    <p className="font-semibold text-green-600">
                      ${calculateAmount(booking, facility).toFixed(2)}
                    </p>
                    <p className="text-xs text-muted-foreground">
                      ${facility?.pricePerHour || 0}/hr
                    </p>
                  </div>

                  <DropdownMenu>
                    <DropdownMenuTrigger asChild>
                      <Button variant="ghost" size="sm">
                        <MoreVertical className="h-4 w-4" />
                      </Button>
                    </DropdownMenuTrigger>
                    <DropdownMenuContent align="end">
                      <Dialog>
                        <DialogTrigger asChild>
                          <DropdownMenuItem
                            onSelect={(e) => e.preventDefault()}
                          >
                            View Details
                          </DropdownMenuItem>
                        </DialogTrigger>
                        <BookingDetailsModal booking={booking} />
                      </Dialog>
                      {booking.status === "active" && (
                        <DropdownMenuItem
                          onClick={() => handleCancelBooking(booking.id)}
                          className="text-red-600"
                        >
                          Cancel Booking
                        </DropdownMenuItem>
                      )}
                    </DropdownMenuContent>
                  </DropdownMenu>
                </div>
              </div>

              {/* Mobile time info */}
              <div className="md:hidden mt-3 pt-3 border-t">
                <div className="flex justify-between text-sm">
                  <div>
                    <span className="text-muted-foreground">Start: </span>
                    <span>{formatDateShort(new Date(booking.startTime))}</span>
                  </div>
                  <div>
                    <span className="text-muted-foreground">End: </span>
                    <span>{formatDateShort(new Date(booking.endTime))}</span>
                  </div>
                  <div>
                    <span className="font-semibold text-green-600">
                      ${calculateAmount(booking, facility).toFixed(2)}
                    </span>
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>
        );
      })}
    </div>
  );

  return (
    <div className="space-y-4">
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-2xl font-bold">My Bookings</h2>
          <p className="text-muted-foreground">
            Manage your parking reservations
          </p>
        </div>
        <div className="flex items-center gap-2">
          <Badge variant="secondary">{bookings.length} bookings</Badge>
          <div className="flex items-center border rounded-lg p-1">
            <Button
              variant={viewMode === "grid" ? "default" : "ghost"}
              size="sm"
              onClick={() => setViewMode("grid")}
              className="h-8 w-8 p-0"
            >
              <Grid3X3 className="h-4 w-4" />
            </Button>
            <Button
              variant={viewMode === "list" ? "default" : "ghost"}
              size="sm"
              onClick={() => setViewMode("list")}
              className="h-8 w-8 p-0"
            >
              <List className="h-4 w-4" />
            </Button>
          </div>
        </div>
      </div>

      {viewMode === "grid" ? <GridView /> : <ListView />}
    </div>
  );
}
