"use client";

import { useState } from "react";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { Search, MapPin, Navigation } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Slider } from "@/components/ui/slider";
import {
  locationSearchSchema,
  type LocationSearch,
  type Facility,
} from "@/lib/schemas";
import { useParkingStore } from "@/hooks/use-parking-store";
import { toast } from "sonner";
import { z } from "zod";

interface LocationSearchProps {
  onFacilitiesFound?: (facilities: Facility[]) => void;
}

// Simulated geocoding for demo/mock purposes
const simulateGeocoding = async (
  query: string
): Promise<{ latitude: number; longitude: number }> => {
  await new Promise((resolve) => setTimeout(resolve, 1000));

  const locations: Record<string, { latitude: number; longitude: number }> = {
    "new york": { latitude: 40.7128, longitude: -74.006 },
    "los angeles": { latitude: 34.0522, longitude: -118.2437 },
    chicago: { latitude: 41.8781, longitude: -87.6298 },
    houston: { latitude: 29.7604, longitude: -95.3698 },
    phoenix: { latitude: 33.4484, longitude: -112.074 },
    philadelphia: { latitude: 39.9526, longitude: -75.1652 },
    "san antonio": { latitude: 29.4241, longitude: -98.4936 },
    "san diego": { latitude: 32.7157, longitude: -117.1611 },
    dallas: { latitude: 32.7767, longitude: -96.797 },
    "san jose": { latitude: 37.3382, longitude: -121.8863 },
  };

  const normalizedQuery = query.toLowerCase();

  for (const [city, coords] of Object.entries(locations)) {
    if (normalizedQuery.includes(city)) {
      return coords;
    }
  }

  return {
    latitude: 40.7128 + (Math.random() - 0.5) * 0.2,
    longitude: -74.006 + (Math.random() - 0.5) * 0.2,
  };
};

export function LocationSearchComponent({
  onFacilitiesFound,
}: LocationSearchProps) {
  const [searchResults, setSearchResults] = useState<Facility[]>([]);
  const [isSearching, setIsSearching] = useState(false);
  const searchFacilitiesByLocation = useParkingStore(
    (state) => state.searchFacilitiesByLocation
  );

  const {
    register,
    handleSubmit,
    setValue,
    watch,
    formState: { errors },
  } = useForm<LocationSearch>({
    resolver: zodResolver(locationSearchSchema),
    defaultValues: {
      query: "",
      radius: 10,
      latitude: undefined,
      longitude: undefined,
    },
  });

  const radius = watch("radius");

  const getCurrentLocation = () => {
    setIsSearching(true);
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          const { latitude, longitude } = position.coords;
          setValue("latitude", latitude);
          setValue("longitude", longitude);
          setValue("query", `${latitude.toFixed(4)}, ${longitude.toFixed(4)}`);

          const facilities = searchFacilitiesByLocation(
            latitude,
            longitude,
            radius
          );
          setSearchResults(facilities);
          onFacilitiesFound?.(facilities);
          setIsSearching(false);
        },
        (error) => {
          console.error("Error getting location:", error);
          setIsSearching(false);
        }
      );
    }
  };

  const onSubmit = async (data: z.infer<typeof locationSearchSchema>) => {
    setIsSearching(true);

    try {
      if (data.latitude && data.longitude) {
        const facilities = searchFacilitiesByLocation(
          data.latitude,
          data.longitude,
          data.radius
        );
        setSearchResults(facilities);
        onFacilitiesFound?.(facilities);
      } else {
        const mockCoordinates = await simulateGeocoding(data.query);

        setValue("latitude", mockCoordinates.latitude);
        setValue("longitude", mockCoordinates.longitude);

        const facilities = searchFacilitiesByLocation(
          mockCoordinates.latitude,
          mockCoordinates.longitude,
          data.radius
        );
        setSearchResults(facilities);
        onFacilitiesFound?.(facilities);
      }
    } catch (error) {
      console.error("Search error:", error);
      toast.error("Search failed. Please try again.");
    } finally {
      setIsSearching(false);
    }
  };

  return (
    <Card>
      <CardHeader>
        <CardTitle className="flex items-center gap-2">
          <Search className="h-5 w-5" />
          Find Parking Near You
        </CardTitle>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit(onSubmit)} className="space-y-4">
          <div className="flex gap-2">
            <div className="flex-1 space-y-2">
              <Label htmlFor="query">Location</Label>
              <Input
                id="query"
                {...register("query")}
                placeholder="Enter address, city, or landmark"
                className="flex-1"
              />
              {errors.query && (
                <p className="text-sm text-red-500">{errors.query.message}</p>
              )}
            </div>
            <div className="flex flex-col justify-end">
              <Button
                type="button"
                variant="outline"
                size="icon"
                onClick={getCurrentLocation}
                disabled={isSearching}
                title="Use current location"
              >
                <Navigation className="h-4 w-4" />
              </Button>
            </div>
          </div>

          <div className="space-y-2">
            <Label>Search Radius: {radius} km</Label>
            <Slider
              value={[radius]}
              onValueChange={(value) => setValue("radius", value[0])}
              max={50}
              min={1}
              step={1}
              className="w-full"
            />
          </div>

          <Button type="submit" disabled={isSearching} className="w-full">
            {isSearching ? "Searching..." : "Search Facilities"}
          </Button>
        </form>

        {searchResults.length > 0 && (
          <div className="mt-6 space-y-3">
            <h3 className="font-semibold">
              Found {searchResults.length} facilities nearby
            </h3>
            <div className="grid gap-3 max-h-60 overflow-y-auto">
              {searchResults.map((facility) => (
                <div
                  key={facility.id}
                  className="flex items-center gap-3 p-3 border rounded-lg hover:bg-muted/50 transition-colors"
                >
                  <MapPin className="h-4 w-4 text-muted-foreground flex-shrink-0" />
                  <div className="flex-1 min-w-0">
                    <p className="font-medium truncate">{facility.name}</p>
                    <p className="text-sm text-muted-foreground truncate">
                      {facility.address}
                    </p>
                    <div className="flex items-center gap-4 mt-1">
                      <p className="text-sm text-green-600 font-semibold">
                        ${facility.pricePerHour}/hour
                      </p>
                      <p className="text-xs text-muted-foreground">
                        {facility.facilityType}
                      </p>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        )}

        {searchResults.length === 0 && !isSearching && watch("query") && (
          <div className="mt-6 p-4 bg-muted rounded-lg text-center">
            <p className="text-muted-foreground">
              No facilities found. Try a different location or increase the
              search radius.
            </p>
          </div>
        )}
      </CardContent>
    </Card>
  );
}

export { LocationSearchComponent as LocationSearch };
