"use client"

import { useState } from "react"
import { ArrowLeft, ArrowRight } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { FacilityList } from "@/feature/facility/component/facilityList"
import { FloorGrid } from "@/feature/floor/component/floorGrid"
import { PaymentForm } from "@/feature/payment/component/paymentForm"
import { LocationSearch } from "@/feature/search/component/locationSearch"
import { useParkingStore } from "@/hooks/use-parking-store"
import { Facility, Floor, Slot } from "@/lib/schemas"
import { BookingForm } from "./bookingForm"


type WizardStep = "search" | "facility" | "floor" | "slot" | "booking" | "payment"

export function BookingWizard() {
  const [currentStep, setCurrentStep] = useState<WizardStep>("search")
  const [selectedFacility, setSelectedFacility] = useState<Facility | null>(null)
  const [selectedFloor, setSelectedFloor] = useState<Floor | null>(null)
  const [selectedSlot, setSelectedSlot] = useState<Slot | null>(null)
  const [searchResults, setSearchResults] = useState<Facility[]>([])
  const [bookingData, setBookingData] = useState<any>(null)
  const [paymentAmount, setPaymentAmount] = useState(0)

  const getFloorsByFacility = useParkingStore((state) => state.getFloorsByFacility)

  const handleSearchResults = (facilities: Facility[]) => {
    setSearchResults(facilities)
    if (facilities.length > 0) {
      // Don't automatically navigate, let user click the button
      // setCurrentStep("facility")
    }
  }

  const handleFacilitySelect = (facility: Facility) => {
    setSelectedFacility(facility)
    setSelectedFloor(null)
    setSelectedSlot(null)
    setCurrentStep("floor")
  }

  const handleFloorSelect = (floor: Floor) => {
    setSelectedFloor(floor)
    setSelectedSlot(null)
    setCurrentStep("slot")
  }

  const handleSlotSelect = (slot: Slot) => {
    setSelectedSlot(slot)
    setCurrentStep("booking")
  }

  const handleBookingComplete = (booking: any) => {
    setBookingData(booking)
    // Calculate payment amount (hours * price per hour)
    const hours = Math.ceil((booking.endTime - booking.startTime) / (1000 * 60 * 60))
    const amount = hours * (selectedFacility?.pricePerHour || 5)
    setPaymentAmount(amount)
    setCurrentStep("payment")
  }

  const handleBookingSuccess = () => {
    // Reset wizard
    setCurrentStep("facility")
    setSelectedFacility(null)
    setSelectedFloor(null)
    setSelectedSlot(null)
  }

  const handlePaymentSuccess = () => {
    // Reset wizard
    setCurrentStep("search")
    setSelectedFacility(null)
    setSelectedFloor(null)
    setSelectedSlot(null)
    setBookingData(null)
    setSearchResults([])
  }

  const handleBack = () => {
    switch (currentStep) {
      case "facility":
        setCurrentStep("search")
        setSelectedFacility(null)
        break
      case "floor":
        setCurrentStep("facility")
        setSelectedFloor(null)
        break
      case "slot":
        setCurrentStep("floor")
        setSelectedSlot(null)
        break
      case "booking":
        setCurrentStep("slot")
        break
      case "payment":
        setCurrentStep("booking")
        break
    }
  }

  const renderStepContent = () => {
    switch (currentStep) {
      case "search":
        return (
          <div className="space-y-4">
            <div className="text-center">
              <h2 className="text-2xl font-bold">Find Parking</h2>
              <p className="text-muted-foreground">Search for parking facilities near your location</p>
            </div>
            <LocationSearch onFacilitiesFound={handleSearchResults} />
            {searchResults.length > 0 && (
              <div className="mt-6">
                <Button onClick={() => setCurrentStep("facility")} className="w-full">
                  View {searchResults.length} Available Facilities
                </Button>
              </div>
            )}
            {searchResults.length === 0 && searchResults !== null && (
              <div className="mt-6 p-4 bg-muted rounded-lg text-center">
                <p className="text-muted-foreground">
                  No facilities found in your search area. Try expanding your search radius or searching in a different
                  location.
                </p>
              </div>
            )}
          </div>
        )

      case "facility":
        return (
          <div className="space-y-4">
            <div className="flex items-center justify-between">
              <div>
                <h2 className="text-2xl font-bold">Select a Facility</h2>
                <p className="text-muted-foreground">Choose from available parking facilities</p>
              </div>
              <Button variant="outline" onClick={handleBack}>
                <ArrowLeft className="h-4 w-4 mr-2" />
                Back
              </Button>
            </div>
            <FacilityList onSelectFacility={handleFacilitySelect} />
          </div>
        )

      case "floor":
        if (!selectedFacility) return null

        const floors = getFloorsByFacility(selectedFacility.id)

        return (
          <div className="space-y-4">
            <div className="flex items-center justify-between">
              <div>
                <h2 className="text-2xl font-bold">Select a Floor</h2>
                <p className="text-muted-foreground">{selectedFacility.name}</p>
              </div>
              <Button variant="outline" onClick={handleBack}>
                <ArrowLeft className="h-4 w-4 mr-2" />
                Back
              </Button>
            </div>

            <div className="grid gap-4 md:grid-cols-2">
              {floors.map((floor) => (
                <Card
                  key={floor.id}
                  className="cursor-pointer hover:shadow-md transition-shadow"
                  onClick={() => handleFloorSelect(floor)}
                >
                  <CardHeader>
                    <CardTitle className="flex items-center justify-between">
                      {floor.name}
                      <ArrowRight className="h-4 w-4" />
                    </CardTitle>
                  </CardHeader>
                  <CardContent>
                    <p className="text-sm text-muted-foreground">
                      {floor.rows} rows  {floor.columns} columns = {floor.rows * floor.columns} slots
                    </p>
                  </CardContent>
                </Card>
              ))}
            </div>
          </div>
        )

      case "slot":
        if (!selectedFloor) return null

        return (
          <div className="space-y-4">
            <div className="flex items-center justify-between">
              <div>
                <h2 className="text-2xl font-bold">Select a Parking Slot</h2>
                <p className="text-muted-foreground">
                  {selectedFacility?.name}  {selectedFloor.name}
                </p>
              </div>
              <Button variant="outline" onClick={handleBack}>
                <ArrowLeft className="h-4 w-4 mr-2" />
                Back
              </Button>
            </div>

            <FloorGrid floorId={selectedFloor.id} onSlotSelect={handleSlotSelect} selectedSlotId={selectedSlot?.id} />
          </div>
        )

      case "booking":
        if (!selectedFacility || !selectedFloor || !selectedSlot) return null

        return (
          <div className="space-y-4">
            <div className="flex items-center justify-between">
              <div>
                <h2 className="text-2xl font-bold">Complete Your Booking</h2>
                <p className="text-muted-foreground">
                  {selectedFacility.name}  {selectedFloor.name}  Slot {selectedSlot.slotNumber}
                </p>
              </div>
              <Button variant="outline" onClick={handleBack}>
                <ArrowLeft className="h-4 w-4 mr-2" />
                Back
              </Button>
            </div>

            <BookingForm
              facilityId={selectedFacility.id}
              floorId={selectedFloor.id}
              slotId={selectedSlot.id}
              onSuccess={handleBookingComplete}
              onCancel={handleBack}
            />
          </div>
        )

      case "payment":
        if (!bookingData) return null

        return (
          <div className="space-y-4">
            <div className="flex items-center justify-between">
              <div>
                <h2 className="text-2xl font-bold">Complete Payment</h2>
                <p className="text-muted-foreground">
                  {selectedFacility?.name}  {selectedFloor?.name}  Slot {selectedSlot?.slotNumber}
                </p>
              </div>
              <Button variant="outline" onClick={handleBack}>
                <ArrowLeft className="h-4 w-4 mr-2" />
                Back
              </Button>
            </div>

            <PaymentForm
              bookingId={bookingData.id}
              amount={paymentAmount}
              onSuccess={handlePaymentSuccess}
              onCancel={handleBack}
            />
          </div>
        )

      default:
        return null
    }
  }

  return <div className="container mx-auto py-8 px-4">{renderStepContent()}</div>
}
