"use client";

import { useForm, useFieldArray } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { toast } from "sonner";
import { Plus, Trash2, Clock, Phone, Mail } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Checkbox } from "@/components/ui/checkbox";
import { Separator } from "@/components/ui/separator";
import {
  createFacilityWithFloorsSchema,
  type CreateFacilityWithFloorsInput,
} from "@/lib/schemas";
import { useParkingStore } from "@/hooks/use-parking-store";

interface FacilityWithFloorsFormProps {
  onSuccess?: () => void;
}

const facilityTypes = [
  { value: "mall", label: "Shopping Mall" },
  { value: "office", label: "Office Building" },
  { value: "hospital", label: "Hospital" },
  { value: "airport", label: "Airport" },
  { value: "residential", label: "Residential" },
  { value: "commercial", label: "Commercial" },
];

const availableAmenities = [
  "24/7 Security",
  "CCTV Surveillance",
  "Electric Vehicle Charging",
  "Covered Parking",
  "Valet Service",
  "Car Wash",
  "Restrooms",
  "Elevator Access",
  "Disabled Access",
  "Online Booking",
];

const defaultValues: CreateFacilityWithFloorsInput = {
  facility: {
    name: "",
    address: "",
    facilityType: "mall",
    amenities: [],
    openingTime: "06:00",
    closingTime: "22:00",
    contactPhone: "",
    contactEmail: "",
    latitude: 0,
    longitude: 0,
    pricePerHour: 5,
    totalFloors: 4,
  },
  floors: [
    { name: "Ground Floor", floorNumber: 1, rows: 5, columns: 10 },
    { name: "First Floor", floorNumber: 2, rows: 5, columns: 10 },
    { name: "Second Floor", floorNumber: 3, rows: 5, columns: 10 },
    { name: "Third Floor", floorNumber: 4, rows: 5, columns: 10 },
  ],
};

export function FacilityWithFloorsForm({
  onSuccess,
}: FacilityWithFloorsFormProps) {
  const addFacilityWithFloors = useParkingStore(
    (state) => state.addFacilityWithFloors
  );

  const {
    register,
    handleSubmit,
    control,
    setValue,
    watch,
    reset,
    formState: { errors, isSubmitting },
  } = useForm<CreateFacilityWithFloorsInput>({
    resolver: zodResolver(createFacilityWithFloorsSchema),
    defaultValues,
  });

  const { fields, append, remove } = useFieldArray({
    control,
    name: "floors",
  });

  const selectedAmenities = watch("facility.amenities");

  const onSubmit = async (data: CreateFacilityWithFloorsInput) => {
    try {
      addFacilityWithFloors(data);
      toast.success(
        `Facility "${data.facility.name}" created with ${data.floors.length} floors!`
      );

      // Clear the form after successful submission
      reset(defaultValues);

      onSuccess?.();
    } catch (error) {
      toast.error("Failed to create facility");
    }
  };

  const handleAmenityChange = (amenity: string, checked: boolean) => {
    const current = selectedAmenities || [];
    if (checked) {
      setValue("facility.amenities", [...current, amenity]);
    } else {
      setValue(
        "facility.amenities",
        current.filter((a) => a !== amenity)
      );
    }
  };

  const handleClearForm = () => {
    reset(defaultValues);
    toast.info("Form cleared");
  };

  return (
    <Card>
      <CardHeader>
        <CardTitle>Create Facility with Floors</CardTitle>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
          {/* Facility Information */}
          <div className="space-y-4">
            <h3 className="text-lg font-semibold">Facility Information</h3>

            <div className="grid grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label htmlFor="facility.name">Facility Name</Label>
                <Input
                  id="facility.name"
                  {...register("facility.name")}
                  placeholder="Enter facility name"
                />
                {errors.facility?.name && (
                  <p className="text-sm text-red-500">
                    {errors.facility.name.message}
                  </p>
                )}
              </div>

              <div className="space-y-2">
                <Label htmlFor="facility.facilityType">Facility Type</Label>
                <Select
                  value={watch("facility.facilityType")}
                  onValueChange={(value: any) =>
                    setValue("facility.facilityType", value)
                  }
                >
                  <SelectTrigger>
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent>
                    {facilityTypes.map((type) => (
                      <SelectItem key={type.value} value={type.value}>
                        {type.label}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
            </div>

            <div className="space-y-2">
              <Label htmlFor="facility.address">Address</Label>
              <Input
                id="facility.address"
                {...register("facility.address")}
                placeholder="Enter facility address"
              />
              {errors.facility?.address && (
                <p className="text-sm text-red-500">
                  {errors.facility.address.message}
                </p>
              )}
            </div>

            <div className="grid grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label htmlFor="facility.latitude">Latitude</Label>
                <Input
                  id="facility.latitude"
                  type="number"
                  step="any"
                  {...register("facility.latitude", { valueAsNumber: true })}
                  placeholder="e.g., 40.7128"
                />
                {errors.facility?.latitude && (
                  <p className="text-sm text-red-500">
                    {errors.facility.latitude.message}
                  </p>
                )}
              </div>

              <div className="space-y-2">
                <Label htmlFor="facility.longitude">Longitude</Label>
                <Input
                  id="facility.longitude"
                  type="number"
                  step="any"
                  {...register("facility.longitude", { valueAsNumber: true })}
                  placeholder="e.g., -74.0060"
                />
                {errors.facility?.longitude && (
                  <p className="text-sm text-red-500">
                    {errors.facility.longitude.message}
                  </p>
                )}
              </div>
            </div>

            <div className="grid grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label htmlFor="facility.contactPhone">Contact Phone</Label>
                <div className="relative">
                  <Phone className="absolute left-3 top-3 h-4 w-4 text-muted-foreground" />
                  <Input
                    id="facility.contactPhone"
                    {...register("facility.contactPhone")}
                    placeholder="Enter phone number"
                    className="pl-10"
                  />
                </div>
                {errors.facility?.contactPhone && (
                  <p className="text-sm text-red-500">
                    {errors.facility.contactPhone.message}
                  </p>
                )}
              </div>

              <div className="space-y-2">
                <Label htmlFor="facility.contactEmail">Contact Email</Label>
                <div className="relative">
                  <Mail className="absolute left-3 top-3 h-4 w-4 text-muted-foreground" />
                  <Input
                    id="facility.contactEmail"
                    type="email"
                    {...register("facility.contactEmail")}
                    placeholder="Enter email address"
                    className="pl-10"
                  />
                </div>
                {errors.facility?.contactEmail && (
                  <p className="text-sm text-red-500">
                    {errors.facility.contactEmail.message}
                  </p>
                )}
              </div>
            </div>

            <div className="grid grid-cols-3 gap-4">
              <div className="space-y-2">
                <Label htmlFor="facility.openingTime">Opening Time</Label>
                <div className="relative">
                  <Clock className="absolute left-3 top-3 h-4 w-4 text-muted-foreground" />
                  <Input
                    id="facility.openingTime"
                    type="time"
                    {...register("facility.openingTime")}
                    className="pl-10"
                  />
                </div>
                {errors.facility?.openingTime && (
                  <p className="text-sm text-red-500">
                    {errors.facility.openingTime.message}
                  </p>
                )}
              </div>

              <div className="space-y-2">
                <Label htmlFor="facility.closingTime">Closing Time</Label>
                <div className="relative">
                  <Clock className="absolute left-3 top-3 h-4 w-4 text-muted-foreground" />
                  <Input
                    id="facility.closingTime"
                    type="time"
                    {...register("facility.closingTime")}
                    className="pl-10"
                  />
                </div>
                {errors.facility?.closingTime && (
                  <p className="text-sm text-red-500">
                    {errors.facility.closingTime.message}
                  </p>
                )}
              </div>

              <div className="space-y-2">
                <Label htmlFor="facility.pricePerHour">
                  Price per Hour ($)
                </Label>
                <Input
                  id="facility.pricePerHour"
                  type="number"
                  min="0"
                  step="0.01"
                  {...register("facility.pricePerHour", {
                    valueAsNumber: true,
                  })}
                  placeholder="5.00"
                />
                {errors.facility?.pricePerHour && (
                  <p className="text-sm text-red-500">
                    {errors.facility.pricePerHour.message}
                  </p>
                )}
              </div>
            </div>

            <div className="space-y-2">
              <Label>Amenities</Label>
              <div className="grid grid-cols-2 gap-2">
                {availableAmenities.map((amenity) => (
                  <div key={amenity} className="flex items-center space-x-2">
                    <Checkbox
                      id={amenity}
                      checked={selectedAmenities?.includes(amenity) || false}
                      onCheckedChange={(checked) =>
                        handleAmenityChange(amenity, checked as boolean)
                      }
                    />
                    <Label htmlFor={amenity} className="text-sm">
                      {amenity}
                    </Label>
                  </div>
                ))}
              </div>
            </div>
          </div>

          <Separator />

          {/* Floors Configuration */}
          <div className="space-y-4">
            <div className="flex items-center justify-between">
              <h3 className="text-lg font-semibold">Floor Configuration</h3>
              <Button
                type="button"
                variant="outline"
                size="sm"
                onClick={() =>
                  append({
                    name: `Floor ${fields.length + 1}`,
                    floorNumber: fields.length + 1,
                    rows: 5,
                    columns: 10,
                  })
                }
              >
                <Plus className="h-4 w-4 mr-2" />
                Add Floor
              </Button>
            </div>

            <div className="space-y-4">
              {fields.map((field, index) => (
                <Card key={field.id} className="p-4">
                  <div className="flex items-center justify-between mb-4">
                    <h4 className="font-medium">Floor {index + 1}</h4>
                    {fields.length > 4 && (
                      <Button
                        type="button"
                        variant="outline"
                        size="sm"
                        onClick={() => remove(index)}
                      >
                        <Trash2 className="h-4 w-4" />
                      </Button>
                    )}
                  </div>

                  <div className="grid grid-cols-3 gap-4">
                    <div className="space-y-2">
                      <Label htmlFor={`floors.${index}.name`}>Floor Name</Label>
                      <Input
                        {...register(`floors.${index}.name`)}
                        placeholder="e.g., Ground Floor"
                      />
                    </div>

                    <div className="space-y-2">
                      <Label htmlFor={`floors.${index}.rows`}>Rows</Label>
                      <Input
                        type="number"
                        min="1"
                        max="26"
                        {...register(`floors.${index}.rows`, {
                          valueAsNumber: true,
                        })}
                      />
                    </div>

                    <div className="space-y-2">
                      <Label htmlFor={`floors.${index}.columns`}>Columns</Label>
                      <Input
                        type="number"
                        min="1"
                        {...register(`floors.${index}.columns`, {
                          valueAsNumber: true,
                        })}
                      />
                    </div>
                  </div>

                  <div className="mt-2 text-sm text-muted-foreground">
                    Total slots:{" "}
                    {(watch(`floors.${index}.rows`) || 0) *
                      (watch(`floors.${index}.columns`) || 0)}
                  </div>
                </Card>
              ))}
            </div>
          </div>

          <div className="flex gap-2 pt-4">
            <Button
              type="button"
              variant="outline"
              onClick={handleClearForm}
              className="flex-1 bg-transparent"
            >
              Clear Form
            </Button>
            <Button type="submit" disabled={isSubmitting} className="flex-1">
              {isSubmitting ? "Creating..." : "Create Facility with Floors"}
            </Button>
          </div>
        </form>
      </CardContent>
    </Card>
  );
}
