"use client";

import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { toast } from "sonner";
import { CalendarIcon } from "lucide-react";
import { format } from "date-fns";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "@/components/ui/popover";
import { Calendar } from "@/components/ui/calendar";
import { createBookingSchema, type CreateBookingInput } from "@/lib/schemas";
import { useParkingStore } from "@/hooks/use-parking-store";
import { cn } from "@/lib/utils";
import { generateSlotId } from "@/lib/settings";

interface BookingFormProps {
  facilityId: string;
  floorId: string;
  slotId: string;
  onSuccess?: (bookingData: unknown) => void;
  onCancel?: () => void;
}

export function BookingForm({
  facilityId,
  floorId,
  slotId,
  onSuccess,
  onCancel,
}: BookingFormProps) {
  const addBooking = useParkingStore((state) => state.addBooking);
  const facility = useParkingStore((state) =>
    state.getFacilityById(facilityId)
  );
  const floor = useParkingStore((state) =>
    state.floors.find((f) => f.id === floorId)
  );
  const slot = useParkingStore((state) =>
    state.slots.find((s) => s.id === slotId)
  );

  const {
    register,
    handleSubmit,
    setValue,
    watch,
    formState: { errors, isSubmitting },
  } = useForm<CreateBookingInput>({
    resolver: zodResolver(createBookingSchema),
    defaultValues: {
      facilityId,
      floorId,
      slotId,
      vehicleType: slot?.vehicleType || "car",
      startTime: new Date(),
      endTime: new Date(Date.now() + 2 * 60 * 60 * 1000), // 2 hours from now
    },
  });

  const startTime = watch("startTime");
  const endTime = watch("endTime");

  const onSubmit = async (data: CreateBookingInput) => {
    try {
      if (data.endTime <= data.startTime) {
        toast.error("End time must be after start time");
        return;
      }

      addBooking({
        ...data,
        status: "active",
      });
      toast.success(`Slot ${slot?.slotNumber} booked successfully!`);

      // Pass the booking data to the parent component
      const bookingData = {
        id: generateSlotId(), // Generate a temporary ID for the booking
        ...data,
        startTime: data.startTime,
        endTime: data.endTime,
      };

      onSuccess?.(bookingData);
    } catch {
      toast.error("Failed to create booking");
    }
  };

  if (!facility || !floor || !slot) {
    return (
      <Card>
        <CardContent className="py-8">
          <p className="text-center text-muted-foreground">
            Booking information not found
          </p>
        </CardContent>
      </Card>
    );
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle>Book Parking Slot</CardTitle>
        <div className="text-sm text-muted-foreground">
          {facility.name}  {floor.name}  Slot {slot.slotNumber}
        </div>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit(onSubmit)} className="space-y-4">
          <div className="grid grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label htmlFor="customerName">Full Name</Label>
              <Input
                id="customerName"
                {...register("customerName")}
                placeholder="Enter your full name"
              />
              {errors.customerName && (
                <p className="text-sm text-red-500">
                  {errors.customerName.message}
                </p>
              )}
            </div>

            <div className="space-y-2">
              <Label htmlFor="customerEmail">Email</Label>
              <Input
                id="customerEmail"
                type="email"
                {...register("customerEmail")}
                placeholder="Enter your email"
              />
              {errors.customerEmail && (
                <p className="text-sm text-red-500">
                  {errors.customerEmail.message}
                </p>
              )}
            </div>
          </div>

          <div className="grid grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label htmlFor="customerPhone">Phone Number</Label>
              <Input
                id="customerPhone"
                {...register("customerPhone")}
                placeholder="Enter your phone number"
              />
              {errors.customerPhone && (
                <p className="text-sm text-red-500">
                  {errors.customerPhone.message}
                </p>
              )}
            </div>

            <div className="space-y-2">
              <Label htmlFor="vehicleNumber">Vehicle Number</Label>
              <Input
                id="vehicleNumber"
                {...register("vehicleNumber")}
                placeholder="e.g., ABC-1234"
              />
              {errors.vehicleNumber && (
                <p className="text-sm text-red-500">
                  {errors.vehicleNumber.message}
                </p>
              )}
            </div>
          </div>

          <div className="space-y-2">
            <Label htmlFor="vehicleType">Vehicle Type</Label>
            <Select
              value={watch("vehicleType")}
              onValueChange={(value: "car" | "motorcycle" | "truck") =>
                setValue("vehicleType", value)
              }
            >
              <SelectTrigger>
                <SelectValue />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="car">Car</SelectItem>
                <SelectItem value="motorcycle">Motorcycle</SelectItem>
                <SelectItem value="truck">Truck</SelectItem>
              </SelectContent>
            </Select>
          </div>

          <div className="grid grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label>Start Time</Label>
              <Popover>
                <PopoverTrigger asChild>
                  <Button
                    variant="outline"
                    className={cn(
                      "w-full justify-start text-left font-normal",
                      !startTime && "text-muted-foreground"
                    )}
                  >
                    <CalendarIcon className="mr-2 h-4 w-4" />
                    {startTime ? format(startTime, "PPP p") : "Pick start time"}
                  </Button>
                </PopoverTrigger>
                <PopoverContent className="w-auto p-0" align="start">
                  <Calendar
                    mode="single"
                    selected={startTime}
                    onSelect={(date) => date && setValue("startTime", date)}
                    disabled={(date) => date < new Date()}
                    initialFocus
                  />
                </PopoverContent>
              </Popover>
            </div>

            <div className="space-y-2">
              <Label>End Time</Label>
              <Popover>
                <PopoverTrigger asChild>
                  <Button
                    variant="outline"
                    className={cn(
                      "w-full justify-start text-left font-normal",
                      !endTime && "text-muted-foreground"
                    )}
                  >
                    <CalendarIcon className="mr-2 h-4 w-4" />
                    {endTime ? format(endTime, "PPP p") : "Pick end time"}
                  </Button>
                </PopoverTrigger>
                <PopoverContent className="w-auto p-0" align="start">
                  <Calendar
                    mode="single"
                    selected={endTime}
                    onSelect={(date) => date && setValue("endTime", date)}
                    disabled={(date) => date <= startTime}
                    initialFocus
                  />
                </PopoverContent>
              </Popover>
            </div>
          </div>

          <div className="flex gap-2 pt-4">
            <Button
              type="button"
              variant="outline"
              onClick={onCancel}
              className="flex-1 bg-transparent"
            >
              Cancel
            </Button>
            <Button type="submit" disabled={isSubmitting} className="flex-1">
              {isSubmitting ? "Booking..." : "Confirm Booking"}
            </Button>
          </div>
        </form>
      </CardContent>
    </Card>
  );
}
