"use client"

import { useState } from "react"
import { useForm } from "react-hook-form"
import { zodResolver } from "@hookform/resolvers/zod"
import { toast } from "sonner"
import { CreditCard, Smartphone, Wallet } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group"
import { Separator } from "@/components/ui/separator"
import { createPaymentSchema, type CreatePaymentInput } from "@/lib/schemas"
import { useParkingStore } from "@/hooks/use-parking-store"

interface PaymentFormProps {
  bookingId: string
  amount: number
  onSuccess?: () => void
  onCancel?: () => void
}

const paymentMethods = [
  { value: "card", label: "Credit/Debit Card", icon: CreditCard },
  { value: "paypal", label: "PayPal", icon: Wallet },
  { value: "apple_pay", label: "Apple Pay", icon: Smartphone },
  { value: "google_pay", label: "Google Pay", icon: Smartphone },
]

export function PaymentForm({ bookingId, amount, onSuccess, onCancel }: PaymentFormProps) {
  const [isProcessing, setIsProcessing] = useState(false)
  const addPayment = useParkingStore((state) => state.addPayment)
  const updatePayment = useParkingStore((state) => state.updatePayment)

  const {
    register,
    handleSubmit,
    setValue,
    watch,
    formState: { errors },
  } = useForm<CreatePaymentInput>({
    resolver: zodResolver(createPaymentSchema),
    defaultValues: {
      bookingId,
      amount,
      currency: "USD",
      paymentMethod: "card",
    },
  })

  const selectedPaymentMethod = watch("paymentMethod")

  const simulatePayment = async (): Promise<boolean> => {
    // Simulate payment processing delay
    await new Promise((resolve) => setTimeout(resolve, 2000))

    // Simulate 90% success rate
    return Math.random() > 0.1
  }

  const onSubmit = async (data: CreatePaymentInput) => {
    setIsProcessing(true)

    try {
      // Create payment record
      addPayment({
        ...data,
        status: "pending",
      })

      // Get the payment ID (in real app, this would come from the API response)
      const paymentId = Math.random().toString(36).substr(2, 9)

      toast.loading("Processing payment...", { id: "payment" })

      // Simulate payment processing
      const success = await simulatePayment()

      if (success) {
        // Update payment status
        updatePayment(paymentId, {
          status: "completed",
          transactionId: `txn_${Math.random().toString(36).substr(2, 9)}`,
        })

        toast.success("Payment successful!", { id: "payment" })
        onSuccess?.()
      } else {
        // Update payment status
        updatePayment(paymentId, { status: "failed" })
        toast.error("Payment failed. Please try again.", { id: "payment" })
      }
    } catch {
      toast.error("Payment processing error", { id: "payment" })
    } finally {
      setIsProcessing(false)
    }
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle>Payment Details</CardTitle>
        <div className="text-2xl font-bold text-green-600">
          ${amount.toFixed(2)} {watch("currency")}
        </div>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
          {/* Payment Method Selection */}
          <div className="space-y-3">
            <Label>Payment Method</Label>
            <RadioGroup value={selectedPaymentMethod} 
            onValueChange={(value: "card" | "paypal" | "apple_pay" | "google_pay") => setValue("paymentMethod", value)}>
              {paymentMethods.map((method) => {
                const Icon = method.icon
                return (
                  <div key={method.value} className="flex items-center space-x-2">
                    <RadioGroupItem value={method.value} id={method.value} />
                    <Label htmlFor={method.value} className="flex items-center gap-2 cursor-pointer">
                      <Icon className="h-4 w-4" />
                      {method.label}
                    </Label>
                  </div>
                )
              })}
            </RadioGroup>
          </div>

          <Separator />

          {/* Card Details (only show for card payment) */}
          {selectedPaymentMethod === "card" && (
            <div className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="cardholderName">Cardholder Name</Label>
                <Input id="cardholderName" {...register("cardholderName")} placeholder="Enter cardholder name" />
                {errors.cardholderName && <p className="text-sm text-red-500">{errors.cardholderName.message}</p>}
              </div>

              <div className="space-y-2">
                <Label htmlFor="cardNumber">Card Number</Label>
                <Input id="cardNumber" {...register("cardNumber")} placeholder="1234 5678 9012 3456" maxLength={19} />
                {errors.cardNumber && <p className="text-sm text-red-500">{errors.cardNumber.message}</p>}
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="expiryDate">Expiry Date</Label>
                  <Input id="expiryDate" {...register("expiryDate")} placeholder="MM/YY" maxLength={5} />
                  {errors.expiryDate && <p className="text-sm text-red-500">{errors.expiryDate.message}</p>}
                </div>

                <div className="space-y-2">
                  <Label htmlFor="cvv">CVV</Label>
                  <Input id="cvv" {...register("cvv")} placeholder="123" maxLength={4} />
                  {errors.cvv && <p className="text-sm text-red-500">{errors.cvv.message}</p>}
                </div>
              </div>
            </div>
          )}

          {/* Other payment methods info */}
          {selectedPaymentMethod !== "card" && (
            <div className="p-4 bg-muted rounded-lg">
              <p className="text-sm text-muted-foreground">
                You will be redirected to {paymentMethods.find((m) => m.value === selectedPaymentMethod)?.label} to
                complete your payment.
              </p>
            </div>
          )}

          <Separator />

          {/* Payment Summary */}
          <div className="space-y-2">
            <div className="flex justify-between text-sm">
              <span>Subtotal:</span>
              <span>${amount.toFixed(2)}</span>
            </div>
            <div className="flex justify-between text-sm">
              <span>Processing Fee:</span>
              <span>$0.00</span>
            </div>
            <Separator />
            <div className="flex justify-between font-semibold">
              <span>Total:</span>
              <span>${amount.toFixed(2)}</span>
            </div>
          </div>

          <div className="flex gap-2 pt-4">
            <Button type="button" variant="outline" onClick={onCancel} className="flex-1 bg-transparent">
              Cancel
            </Button>
            <Button type="submit" disabled={isProcessing} className="flex-1">
              {isProcessing ? "Processing..." : `Pay $${amount.toFixed(2)}`}
            </Button>
          </div>
        </form>
      </CardContent>
    </Card>
  )
}
