"use client";

import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { toast } from "sonner";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { createFloorSchema, type CreateFloorInput } from "@/lib/schemas";
import { useParkingStore } from "@/hooks/use-parking-store";

interface FloorFormProps {
  facilityId?: string;
  onSuccess?: () => void;
}

const defaultValues: CreateFloorInput = {
  facilityId: "",
  floorNumber: 1,
  name: "",
  rows: 5,
  columns: 10,
};

export function FloorForm({ facilityId, onSuccess }: FloorFormProps) {
  const facilities = useParkingStore((state) => state.facilities);
  const addFloor = useParkingStore((state) => state.addFloor);

  const {
    register,
    handleSubmit,
    setValue,
    watch,
    reset,
    formState: { errors, isSubmitting },
  } = useForm<CreateFloorInput>({
    resolver: zodResolver(createFloorSchema),
    defaultValues: {
      ...defaultValues,
      facilityId: facilityId || "",
    },
  });

  const selectedFacilityId = watch("facilityId");

  const onSubmit = async (data: CreateFloorInput) => {
    try {
      addFloor(data);
      toast.success(
        `Floor ${data.floorNumber} created with ${
          data.rows * data.columns
        } slots!`
      );

      // Clear the form after successful submission
      reset({
        ...defaultValues,
        facilityId: facilityId || "",
      });

      onSuccess?.();
    } catch (error) {
      toast.error("Failed to create floor");
    }
  };

  const handleClearForm = () => {
    reset({
      ...defaultValues,
      facilityId: facilityId || "",
    });
    toast.info("Form cleared");
  };

  return (
    <Card>
      <CardHeader>
        <CardTitle>Create New Floor</CardTitle>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit(onSubmit)} className="space-y-4">
          {!facilityId && (
            <div className="space-y-2">
              <Label htmlFor="facilityId">Select Facility</Label>
              <Select
                value={selectedFacilityId}
                onValueChange={(value) => setValue("facilityId", value)}
              >
                <SelectTrigger>
                  <SelectValue placeholder="Choose a facility" />
                </SelectTrigger>
                <SelectContent>
                  {facilities.map((facility) => (
                    <SelectItem key={facility.id} value={facility.id}>
                      {facility.name}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
              {errors.facilityId && (
                <p className="text-sm text-red-500">
                  {errors.facilityId.message}
                </p>
              )}
            </div>
          )}

          <div className="grid grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label htmlFor="floorNumber">Floor Number</Label>
              <Input
                id="floorNumber"
                type="number"
                min="1"
                {...register("floorNumber", { valueAsNumber: true })}
                placeholder="e.g., 1"
              />
              {errors.floorNumber && (
                <p className="text-sm text-red-500">
                  {errors.floorNumber.message}
                </p>
              )}
            </div>

            <div className="space-y-2">
              <Label htmlFor="name">Floor Name</Label>
              <Input
                id="name"
                {...register("name")}
                placeholder="e.g., Ground Floor"
              />
              {errors.name && (
                <p className="text-sm text-red-500">{errors.name.message}</p>
              )}
            </div>
          </div>

          <div className="grid grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label htmlFor="rows">Number of Rows</Label>
              <Input
                id="rows"
                type="number"
                min="1"
                max="26"
                {...register("rows", { valueAsNumber: true })}
                placeholder="e.g., 5"
              />
              {errors.rows && (
                <p className="text-sm text-red-500">{errors.rows.message}</p>
              )}
            </div>

            <div className="space-y-2">
              <Label htmlFor="columns">Number of Columns</Label>
              <Input
                id="columns"
                type="number"
                min="1"
                {...register("columns", { valueAsNumber: true })}
                placeholder="e.g., 10"
              />
              {errors.columns && (
                <p className="text-sm text-red-500">{errors.columns.message}</p>
              )}
            </div>
          </div>

          <div className="text-sm text-muted-foreground bg-muted p-3 rounded-md">
            <p>
              Total slots: {(watch("rows") || 0) * (watch("columns") || 0)}
              {watch("rows") && watch("columns") && (
                <span className="ml-2">
                  (Slots will be labeled A1-
                  {String.fromCharCode(64 + (watch("rows") || 0))}
                  {watch("columns") || 0})
                </span>
              )}
            </p>
          </div>

          <div className="flex gap-2">
            <Button
              type="button"
              variant="outline"
              onClick={handleClearForm}
              className="flex-1 bg-transparent"
            >
              Clear Form
            </Button>
            <Button type="submit" disabled={isSubmitting} className="flex-1">
              {isSubmitting ? "Creating..." : "Create Floor"}
            </Button>
          </div>
        </form>
      </CardContent>
    </Card>
  );
}
