"use client";

import { useState } from "react";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { FacilityManagement } from "@/feature/admin/component/FacilityManagement";
import { BookingWizard } from "@/feature/booking/component/bookingWizard";
import { MyBookings } from "@/feature/booking/component/myBookings";
import { FacilityList } from "@/feature/facility/component/facilityList";
import { FacilityWithFloorsForm } from "@/feature/facility/component/facilityWithFloorsForm";
import { FloorForm } from "@/feature/floor/component/floorForm";
import { Toaster } from "sonner";


export default function ParkingManagementSystem() {
  const [activeTab, setActiveTab] = useState("booking");

  return (
    <div className="min-h-screen bg-background">
      <div className="container mx-auto py-8 px-4">
        <div className="mb-8 text-center">
          <h1 className="text-4xl font-bold tracking-tight">
            Parking Management System
          </h1>
          <p className="text-muted-foreground mt-2">
            Manage facilities, floors, and book parking slots with ease
          </p>
        </div>

        <Tabs
          value={activeTab}
          onValueChange={setActiveTab}
          className="space-y-6"
        >
          <TabsList className="grid w-full grid-cols-5">
            <TabsTrigger value="booking">Book Parking</TabsTrigger>
            <TabsTrigger value="my-car">My Bookings</TabsTrigger>
            <TabsTrigger value="facilities">Facilities</TabsTrigger>
            <TabsTrigger value="floors">Floors</TabsTrigger>
            <TabsTrigger value="admin">Admin</TabsTrigger>
          </TabsList>

          <TabsContent value="booking" className="space-y-6">
            <BookingWizard />
          </TabsContent>

          <TabsContent value="my-car" className="space-y-6">
            <MyBookings />
          </TabsContent>

          <TabsContent value="facilities" className="space-y-6">
            <FacilityList />
          </TabsContent>

          <TabsContent value="floors" className="space-y-6">
            <div className="grid gap-6 lg:grid-cols-2">
              <FloorForm />
              <div className="space-y-4">
                <h3 className="text-lg font-semibold">Floor Management</h3>
                <p className="text-muted-foreground">
                  Create floors for your facilities. Each floor will
                  automatically generate parking slots based on the row and
                  column configuration.
                </p>
              </div>
            </div>
          </TabsContent>

          <TabsContent value="admin" className="space-y-6">
            <div className="space-y-8">
              <FacilityManagement />
              <div className="border-t pt-8">
                <h3 className="text-lg font-semibold mb-4">
                  Create New Facility
                </h3>
                <FacilityWithFloorsForm />
              </div>
            </div>
          </TabsContent>
        </Tabs>
      </div>

      <Toaster position="top-right" />
    </div>
  );
}
