"use client";

import { Car, BikeIcon as Motorcycle, Truck } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { cn } from "@/lib/utils";
import { useParkingStore } from "@/hooks/use-parking-store";
import type { Slot } from "@/lib/schemas";

interface FloorGridProps {
  floorId: string;
  onSlotSelect?: (slot: Slot) => void;
  selectedSlotId?: string;
}

const vehicleIcons = {
  car: Car,
  motorcycle: Motorcycle,
  truck: Truck,
};

export function FloorGrid({
  floorId,
  onSlotSelect,
  selectedSlotId,
}: FloorGridProps) {
    
  const { floor, slots } = useParkingStore((state) => {
    const floor = state.floors.find((f) => f.id === floorId);
    const slots = state.getSlotsByFloor(floorId);
    return { floor, slots };
  });

  if (!floor || slots.length === 0) {
    return (
      <div className="text-center py-8 text-muted-foreground">
        No slots found for this floor
      </div>
    );
  }

  // Group slots by row for grid display
  const slotsByRow = slots.reduce((acc, slot) => {
    if (!acc[slot.row]) {
      acc[slot.row] = [];
    }
    acc[slot.row].push(slot);
    return acc;
  }, {} as Record<number, Slot[]>);

  // Sort rows and columns
  const sortedRows = Object.keys(slotsByRow)
    .map(Number)
    .sort((a, b) => a - b);

  return (
    <div className="space-y-4">
      <div className="flex items-center justify-between">
        <h3 className="text-lg font-semibold">{floor.name}</h3>
        <div className="flex gap-2">
          <Badge variant="outline" className="text-green-600">
            Available: {slots.filter((s) => s.isAvailable).length}
          </Badge>
          <Badge variant="outline" className="text-red-600">
            Occupied: {slots.filter((s) => !s.isAvailable).length}
          </Badge>
        </div>
      </div>

      <div className="grid gap-2 p-4 bg-muted/30 rounded-lg overflow-x-auto">
        {sortedRows.map((row) => {
          const rowSlots = slotsByRow[row].sort((a, b) => a.column - b.column);

          return (
            <div key={row} className="flex gap-2 items-center">
              <div className="w-8 text-sm font-medium text-muted-foreground">
                {String.fromCharCode(64 + row)}
              </div>
              <div className="flex gap-1">
                {rowSlots.map((slot) => {
                  const VehicleIcon = vehicleIcons[slot.vehicleType];
                  const isSelected = selectedSlotId === slot.id;

                  return (
                    <Button
                      key={slot.id}
                      variant="outline"
                      size="sm"
                      onClick={() => onSlotSelect?.(slot)}
                      disabled={!slot.isAvailable && !isSelected}
                      className={cn(
                        "h-12 w-16 p-1 flex flex-col items-center justify-center text-xs",
                        slot.isAvailable
                          ? "bg-green-50 border-green-200 hover:bg-green-100"
                          : "bg-red-50 border-red-200 cursor-not-allowed",
                        isSelected && "ring-2 ring-primary",
                        slot.isReserved && "bg-yellow-50 border-yellow-200"
                      )}
                    >
                      <VehicleIcon className="h-3 w-3 mb-1" />
                      <span className="font-mono">{slot.slotNumber}</span>
                    </Button>
                  );
                })}
              </div>
            </div>
          );
        })}
      </div>

      <div className="flex gap-4 text-xs text-muted-foreground">
        <div className="flex items-center gap-1">
          <div className="w-3 h-3 bg-green-100 border border-green-200 rounded"></div>
          <span>Available</span>
        </div>
        <div className="flex items-center gap-1">
          <div className="w-3 h-3 bg-red-100 border border-red-200 rounded"></div>
          <span>Occupied</span>
        </div>
        <div className="flex items-center gap-1">
          <div className="w-3 h-3 bg-yellow-100 border border-yellow-200 rounded"></div>
          <span>Reserved</span>
        </div>
      </div>
    </div>
  );
}
