"use client";

import {
  Building2,
  MapPin,
  Layers,
  Clock,
  Phone,
  DollarSign,
} from "lucide-react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { useParkingStore } from "@/hooks/use-parking-store";
import type { Facility } from "@/lib/schemas";

interface FacilityListProps {
  onSelectFacility?: (facility: Facility) => void;
}

export function FacilityList({ onSelectFacility }: FacilityListProps) {
  const facilities = useParkingStore((state) => state.facilities);
  const getFloorsByFacility = useParkingStore(
    (state) => state.getFloorsByFacility
  );

  if (facilities.length === 0) {
    return (
      <Card>
        <CardContent className="flex flex-col items-center justify-center py-8">
          <Building2 className="h-12 w-12 text-muted-foreground mb-4" />
          <p className="text-muted-foreground text-center">
            No facilities found. Create your first facility to get started.
          </p>
        </CardContent>
      </Card>
    );
  }

  return (
    <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
      {facilities.map((facility) => {
        const floors = getFloorsByFacility(facility.id);

        return (
          <Card key={facility.id} className="hover:shadow-md transition-shadow">
            <CardHeader className="pb-3">
              <div className="flex items-start justify-between">
                <div className="flex items-center gap-2">
                  <Building2 className="h-5 w-5 text-primary" />
                  <CardTitle className="text-lg">{facility.name}</CardTitle>
                </div>
                <Badge variant="secondary">
                  {floors.length}/{facility.totalFloors} floors
                </Badge>
              </div>
            </CardHeader>
            <CardContent className="space-y-3">
              <div className="flex items-center gap-2 text-sm text-muted-foreground">
                <MapPin className="h-4 w-4" />
                <span className="truncate">{facility.address}</span>
              </div>

              <div className="flex items-center gap-2 text-sm text-muted-foreground">
                <Clock className="h-4 w-4" />
                <span>
                  {facility.openingTime} - {facility.closingTime}
                </span>
              </div>

              <div className="flex items-center gap-2 text-sm text-muted-foreground">
                <DollarSign className="h-4 w-4" />
                <span className="font-semibold text-green-600">
                  ${facility.pricePerHour}/hour
                </span>
              </div>

              <div className="flex items-center gap-2 text-sm text-muted-foreground">
                <Phone className="h-4 w-4" />
                <span>{facility.contactPhone}</span>
              </div>

              {facility.amenities && facility.amenities.length > 0 && (
                <div className="flex flex-wrap gap-1 mt-2">
                  {facility.amenities.slice(0, 3).map((amenity) => (
                    <Badge
                      key={amenity}
                      variant="secondary"
                      className="text-xs"
                    >
                      {amenity}
                    </Badge>
                  ))}
                  {facility.amenities.length > 3 && (
                    <Badge variant="secondary" className="text-xs">
                      +{facility.amenities.length - 3} more
                    </Badge>
                  )}
                </div>
              )}

              <div className="flex items-center gap-2 text-sm text-muted-foreground">
                <Layers className="h-4 w-4" />
                <span>
                  {floors.length}/{facility.totalFloors} floors configured
                </span>
              </div>

              {onSelectFacility && (
                <Button
                  onClick={() => onSelectFacility(facility)}
                  className="w-full mt-4"
                  variant="outline"
                >
                  Select Facility
                </Button>
              )}
            </CardContent>
          </Card>
        );
      })}
    </div>
  );
}
